body{
  margin: 0px;
  background-color: blue;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

.centerme{
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;

}

.calcHeader{
  font-weight: 600;
  font-size: 25px;
  color: white;
  font-family: "Montserrat";
  margin-right: 10px;
  margin-bottom: 20px;

}
.calc{
  display: flex;
  flex-direction: column;
  width: 350px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.6);
  border-radius: 20px;
  background-color: #2f2f2f;
  padding: 20px;
  margin-top: 20px;
}
.roundButton{
  font-family: "Exo 2";
  cursor: pointer;
  background-color: #ff7600;
  height: 50px;
  width: 50px;
  border-radius: 25px;
  text-align: center;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  font-weight: 600;
  

}
.screen{
  display: flex;
  background-color:#cecece;
  border-radius: 5px;
  align-items: center;
  font-size: 45px;
  font-weight: 500;
  font-family: "Marvel";
  overflow: hidden;
  justify-content: flex-end;
  height: 80px;
  margin-bottom: 20px;
  padding-right: 10px;

}

.clearButton{
  font-family: "Marvel";
  margin-top: 20px;
  cursor: pointer;
  margin-left: 20px;
  color: white;
  width: max-content;

}

.footer{
  font-family: "Quicksand";
  color: white;
  position: absolute;
  bottom: 10px;
  justify-content: center;
  align-items: center;
  flex: 1;
  text-align: center;
 
}